export default {
  state: {
    locale: 'ru-RU'
  },
  mutations: {
    setLocale (state, newLocale) {
      state.locale = newLocale
    }
  },
  actions: {
    setLocale ({ dispatch, commit }, newLocale) {
      commit('setLocale', newLocale)
    }
  },
  getters: {
    locale: state => state.locale
  }
}
