<script>
export default {
  data: () => ({
    locale: false
  }),
  watch: {
    locale () {
      if (this.locale) {
        this.$store.dispatch('setLocale', 'en-US')
      } else {
        this.$store.dispatch('setLocale', 'ru-RU')
      }
      console.log(this.$store.getters.locale)
    }
  },
  methods: {
    handleScroll: function (evt, el) {
      if (window.scrollY > 50) {
        el.classList.remove('shadow_off')
      } else {
        el.classList.add('shadow_off')
      }
    }
  }
}
</script>

